import {
	USER_NAME,
	USER_AGE
} from '../../common/const.js';
import {
	mapState,
	mapMutations,
	mapGetters,
	mapActions
} from 'vuex';

export default {
	data() {
		return {}
	},
	computed: {
		...mapState({
			userAge1: state => state.user.userAge + '哈哈哈哈',
			userName1: state => state.user.userName + '真可爱啊啊啊'
		}),
		...mapGetters([
			 // 把 `this.userAge` 映射为 `this.$store.getters.userAge`
			"userAge",
			"userName"
		])
	},
	onLoad() {
		// 想要获取getters的内容，但是home是使用命名空间来获取的，所以暂时不知道怎么获取
		// console.log(this.$store.getters)
		console.log(this.$store.state.user.userAge)
		console.log(this.$store.state.user.userName)
	},
	methods: {
		...mapMutations(
			[
				USER_NAME, USER_AGE
			]),
			changeUserName() {
				this.USER_NAME('zhangjiahui')
			},
			changeUserAge() {
				this.USER_AGE(77)
			},
			gotoBack() {
				uni.navigateBack({
					delta: 1
				});
			}
